import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

var months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

var todayDate = new Date();

export function Calendar(props) {
  var [month, setMonth] = useState(props.selectedDate.getMonth());

  function moveDate(para) {
    if (para === "prev") {
      if (month === 0) {
        setMonth(11);
      } else {
        setMonth(month - 1);
      }
    } else if (para === "next") {
      if (month === 11) {
        setMonth(0);
      } else {
        setMonth(month + 1);
      }
    }
  }

  function Cal_head(params) {
    if (todayDate.getDate() > 15) {
      if (todayDate.getMonth() === month) {
        return (
          <div className="month d-flex justify-content-between align-items-center">
            <span></span>

            <div>
              <h2 id="month">{months[month]}</h2>
            </div>

            <div className="next arrow" onClick={() => moveDate("next")}>
              <span>&#10095;</span>
            </div>
          </div>
        );
      } else {
        return (
          <div className="month d-flex justify-content-between align-items-center">
            <div className="prev arrow" onClick={() => moveDate("prev")}>
              <span>&#10094;</span>
            </div>

            <div>
              <h2 id="month">{months[month]}</h2>
            </div>

            <span></span>
          </div>
        );
      }
    } else {
      return (
        <div className="month" style={{ justifyContent: "center" }}>
          <div>
            <h2 id="month">{months[month]}</h2>
          </div>
        </div>
      );
    }
  }
  // renderDate(props.date);
  return (
    <div className="calender card">
      <Cal_head />

      <div className="weekdays">
        <div>Sun</div>
        <div>Mon</div>
        <div>Tue</div>
        <div>Wed</div>
        <div>Thu</div>
        <div>Fri</div>
        <div>Sat</div>
      </div>

      <div
        className="days"
        dangerouslySetInnerHTML={{ __html: getCalendarCells(props.date) }}
      ></div>
    </div>
  );
}

function getCalendarCells(currentDate) {
  currentDate.setDate(1);
  var today = new Date();
  var day = currentDate.getDay();
  var endDate = new Date(
    currentDate.getFullYear(),
    currentDate.getMonth() + 1,
    0
  ).getDate();
  var prevDate = new Date(
    currentDate.getFullYear(),
    currentDate.getMonth(),
    0
  ).getDate();

  var cells = "";
  for (let x = day; x > 0; x--) {
    cells += "<div class='prev_date'>" + (prevDate - x + 1) + "</div>";
  }
  for (var i = 1; i <= endDate; i++) {
    if (i == today.getDate() && currentDate.getMonth() == today.getMonth())
      cells += "<div class='today'>" + i + "</div>";
    else cells += "<div>" + i + "</div>";
  }

  return cells;
}

function CalenderCell() {}

